
import React, { useEffect, useState } from 'react'

function Home({send}) {
    const [data, setData] = useState([]);
    console.log(data);

    useEffect(() => {
        const cartdata = async () => {
            const response = await fetch("https://boppotech.github.io/react-task-json.github.io/reactjob.json");
            //console.log(response);
            const jsonData = await response.json();
            //console.log(jsonData)
            setData(jsonData);
        }
        cartdata();
    }, [])
    
   // const send =(e)=>{
   //     console.log(e);
   // }
    

    return (
        <div className='m-3'>
            <div className='row flex justify-content-center align-items-center '>
                {data.map((element, k) => {
                    return (
                        <div>
                            <div class="card m-4 p-3 card_details" style={{ width: "18rem",border:'none' }}>
                                <img src='https://cdn.shopify.com/s/files/1/0745/3953/2586/products/20210707_113433-260_e06dbbec-8811-4418-add5-d5754b85b944.jpg?v=1680693242' alt=''
                                style={{width:'250px', height:'250px'}} />
                                <div class="card-body">
                                    <h5 class="card-title">{element.title}</h5>
                                     <h5>Price:</h5>
                                     <h6>Items quantity : {element.quantity}</h6>
                                    <button className='btn btn-primary col-lg-12' onClick={()=>send(element)}>Add To Cart</button>
                                </div>
                            </div>
                        </div>
                    )
                })}

            </div>
        </div>
    )
}

export default Home